.gpt-navbar ,
.gpt-navbar-links ,
.gpt-navbar-signs {
    display: flex;
    padding: 10px 30px;
}
.gpt-navbar {
    height: 100px;
    justify-content: space-between;
    align-items: center;
    
}

.gpt-navbar-links{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 40px;
    position: relative;
}
.gpt-navbar-links-logo{
    position: absolute;
    left: 0;
}
.gpt-navbar-links a{
    color: white;
    padding: 0 20px;
    font-size: 18px;
    font-weight: 500;
    font-family: var(--font-family);
}
.gpt-navbar-links a:hover,
.gpt-navbar-signs p:hover,
.gpt-navbar-signs button:hover,
.gpt-navbar-menu svg:hover,
.gpt-navbar-menu-container:hover,
.gpt-navbar-container-signs button:hover,
.gpt-navbar-container-signs p:hover ,
.change:hover{
    color: grey;
    transition: 0.3s;
    cursor: pointer;
}
.gpt-navbar-signs {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
    color: white;
    padding: 0 10px;
    position: absolute;
    right: 0;
}
.gpt-navbar-signs button ,
.gpt-navbar-container-signs button {
    outline: none;
    border: 0;
    padding: 10px 15px;
    border-radius: 5px;
    background-color: var(--color-red);
    color: white;
    font-size: 16px;
    font-weight: 500;
    font-family: var(--font-family);
    cursor: pointer;
}
.gpt-navbar-menu {
    display: none;
    position: relative;

}
.gpt-navbar-menu svg {
    cursor: pointer;
}
.gpt-navbar-menu-container {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: absolute;
    right: 0;
    width: 250px;
    background-color: var(--color-footer);
    height: 300px;
    border-radius: 10px;
    box-shadow: 0 0 5px;
    padding: 15px;
    transition: 0.3s;
    cursor: pointer;


}
.gpt-navbar-menu-links p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 18px;
    margin:1rem 1.5rem ;

}
.gpt-navbar-container-signs {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
}

@media screen and (max-width: 1050px) {
    .gpt-navbar {
        padding : 2rem 4rem;
    }
    .gpt-navbar img {
        width: 80px;
    }
    .gpt-navbar-links-container {
        display: none;
    }

    .gpt-navbar-menu{
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        position: absolute;
        right: 0;
        color: white;
       

    }
    .gpt-navbar-menu:hover {
        color: grey;
        transition: 0.3s;
        cursor: pointer;
       
    }
    .gpt-navbar-menu-container{
        top: 30px;
    }
   .gpt-navbar-signs{
    margin-right: 40px;
   }
    
}
@media screen and (max-width: 550px) {
    .gpt-navbar {
        padding:3rem 2rem;
    }
    
    .gpt-navbar-signs{
        display: none;
    }
    .gpt-navbar-menu-container{

        width: 200px;
        height: 255px;
    }
    .gpt-navbar-menu-links p {
        gap: 11px;
        margin:0.5rem ;
    }
    .gpt-navbar button{
        padding:6px 10px ;
    }
}

