.gpt-cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color :white;
    margin: 0 auto;
    border-radius: 5px;
    width: 70%;
     background: var(--gradiant-text);
     color: black;
     padding:30px 40px;

}
.gpt-cta-content p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 30px;
    font-weight: 500;
}
.gpt-cta-content h3{
    font-size: 24px;
    font-family: var(--font-family);
    line-height: 45px;
    font-weight: 800;
}
.gpt-cta-btn button{
    padding:10px 40px;
    border-radius:40px ;
    background-color: black;
    color: white;
    cursor: pointer;
    outline: none;
    border: none;
    min-width:150px

}

@media screen and (max-width:1050px) {
    .gpt-cta{
        padding: 20px;
        gap: 10px;
    }
    .gpt-cta-content h3{
        font-size: 22px;
        line-height: 30px;
    }
   
}

@media screen and (max-width:650px) {
    .gpt-cta {
        flex-direction: column;
    }
    .gpt-cta-btn{
        margin: 20px 40px;
    }
    
    
}
@media screen and (max-width:550px) {
    .gpt-cta-content h3{
        font-size: 20px;
    }

}