.gpt-whatgpt {
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    padding: 2rem;
    height: 100vh;
    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt-whatgpt-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
    padding: 0 25px;
   
}
.gpt-whatgpt-heading h1{
    letter-spacing: 0.8px;
    font-size: 33px;
    font-family: var(--font-family);
    font-weight: 800;
    max-width: 510px;
}


.gpt-whatgpt-heading p {
    color: var(--color-subtext);
    margin-top: 20px;
    font-size: 20px;
    font-family: var(--font-family);
    cursor: pointer;

}

.gpt-whatgpt-container{
    display: flex;
    flex-direction: row;
  flex-wrap: wrap;

 
    
   
}
.gpt-whatgpt-container .gpt-features-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 410px;
    margin-top: 30px;
    gap: 40px;

}


@media screen and (max-width:1200px){

    .gpt-whatgpt {
        height: fit-content;
    }

    .gpt-features-text {

        max-width: 400px;

    }
}

@media screen and (max-width:1000px){

   
    .gpt-features-text {

        max-width: 300px;

    }
  
   
}
@media screen and (max-width:769px){
  

    .gpt-whatgpt{
        display: flex;
        flex-direction: column;
        
    }
    .gpt-feature-container{
   
        flex-direction: column;
       
      

    }
    .gpt-whatgpt-feature,
     .gpt-features-container{
        display: flex;
        flex-direction: column;
       justify-content: center;
       align-items: center;
       width: 100%;
       gap: 20px;
    }
    .gpt-whatgpt-heading {
        flex-direction: column;
        gap: 20px;
    }
    .gpt-whatgpt-heading h1{
        font-size: 20px;
    
    }

  

}

@media screen and (max-width : 550px) {
    .gpt-whatgpt-container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .gpt-whatgpt-container p{
     line-height: 20px;
    }
    .gpt-whatgpt-feature .gpt-features-title h1{
        margin: 0 auto;
        

    }
    .gpt-whatgpt-feature .gpt-features-text p{
        width: 220px;

    }
}