.gpt-possibility {
    display: flex;
    margin-left: 130px;

}
.gpt-possibility-image{
    display: flex;
    flex: 1;
}
.gpt-possibility-image img{
   width: 560px;
    height: 607px;
}
.gpt-possibility-content{
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: flex-end;
    gap: 15px;

}
.gpt-possibility-content h1{
    font-weight: 800;
    font-size: 34px;
    line-height:45px;
    max-width: 400px;
    font-family: var(--font-family);

}
.gpt-possibility-content h4{
    font-weight: 500;
    font-size: 16px;
    font-family: var(--font-family);
    line-height:30px;
    color: var(--color-text);
    
}
.gpt-possibility-content h4:last-child{
    color: var(--color-subtext);
    font-weight: 500;
    font-size: 16px;
    line-height:30px;
    font-family: var(--font-family);
    
    
}
.gpt-possibility-content p{
    font-weight: 400;
    font-size: 16px;
    line-height:25px;
    font-family: var(--font-family);
    color:var(--color-text);
   width: 365px;
    
}
@media screen and (max-width:1050px){
    .gpt-possibility-content{
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        position: relative;
       

    }
    .gpt-possibility-content h1,
    .gpt-possibility-content h4,
    .gpt-possibility-content p
    {
        max-width: 400px;
        text-align: left;
    }
    .gpt-possibility-content h4{
       
        
    }
    .gpt-possibility-image img {
        max-width: 300px;
    }
}

@media screen and (max-width:768px) {
    .gpt-possibility{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 10px 0;
    }
    .gpt-possibility-image img{
        width: 380px;
    }
    .gpt-possibility-content{
        margin-top: 40px;
     
    }
    .gpt-possibility-content h1{
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width:576px){
    .gpt-possibility-image img{
        width: 80%;
    }
    .gpt-possibility-content{
        text-align: center;
        padding: 0 10px;
       
        
    }
    .gpt-possibility-content h1{
        font-size: 16px;
        line-height: 20px;
    }
    .gpt-possibility-content p{
        line-height: 20px;
        padding: 0 25px;
    
    }

}