.gpt-feature {
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: row;


}
.gpt-feature-content {
    flex: 1;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    margin-right: 5rem;
    margin-top: 2rem;
    position: relative;
   
}
.gpt-feature-content h1{
    font-size: 34px;
    display: flex;
    font-weight:800;
    line-height: 45px;
    width: 400px;
}
.gpt-feature-content p {
    color: rgba(244, 152, 103, 1);
    font-size: 16px;
    font-family: var(--font-family);
    margin-top: 2rem;
    font-weight: 500;
    line-height: 30px;

}
.gpt-feature-container{
    flex: 1.5;
    display: flex;
    flex-direction: column;
    max-width: 800px;
}
.gpt-feature-container .gpt-features-title {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    
}
.gpt-feature-container .gpt-features-title div{
    position: absolute;
    left: 0;
    top: -10px;
}
.gpt-feature-container .gpt-features-title h1 {
    font-size: 22px;
    font-family: var(--font-family);
    line-height: 35px;

}

@media screen and (max-width:1050px) {
    .gpt-feature {
        flex-direction: column;


        
   
    }

.gpt-feature-content {
    justify-content: space-between;
    align-items: center;
    margin-left: 80px;
}
.gpt-feature-content h1 {

    text-align: center;
    font-size: 28px;

}


    .gpt-feature-container .gpt-features-title h1 {
        font-size: 19px;
        line-height: 25px;

       
    
    }
    .gpt-features-title , .gpt-features-text {
        text-align: center;
    }
   
    .gpt-features-title div {
        margin: 0 40px;
    

    }

}

@media screen and (max-width:576px) {
    .gpt-feature-container{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .gpt-feature-content h1{
        font-size: 15px;
        line-height: 25px;
        width: 200px;
        text-align: left;
    }
    .gpt-feature-content p{
        line-height: 25px;
    }
    .gpt-feature-container .gpt-features-container{
        flex-direction: column;
        gap: 12px;
        text-align: left;
    }
    .gpt-features-container .gpt-features-title h1{
        font-size: 10px;
        line-height: 20px;

       

    }
    .gpt-features-container .gpt-features-title div{
        margin: 0 auto;
        position: absolute;
        right:0;
        transform: translate(-50%,-50%);
    }
   

    
}
@media screen and (max-width:425px){
    .gpt-feature-content h1{
        width: 300px;
        padding: 0 5px;
    }
    .gpt-features-container .gpt-features-title h1{
        line-height: 20px;
        font-size: 10px;
        margin: 0 auto;

    }
   
    
}

