.gpt-blog-article {

    background: var(--color-footer);
    border-radius: 5px;
    padding: 0;
    display: flex;
    flex-direction: column;
   

    
}
.gpt-blog-article-image {
    
    background: var(--color-footer);


}
.gpt-blog-article-image img{
    width: 100%;
    padding: 0;
    height: 100%;

}
.gpt-blog-article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 350px;
    position: relative;
}
.gpt-blog-article-content p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 35px;
    font-weight: 700;
    color: var(--color-white);
  

}
.gpt-blog-article-content h2{
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 30px;
    font-weight: 800;
    color: var(--color-white);
    margin-bottom: 5rem;
    cursor: pointer;

}
.gpt-blog-article-content p:last-child{
    font-family: var(--font-family);
    font-size: 13px;
    line-height: 35px;
    font-weight: 700;
    color: var(--color-white);
    cursor: pointer;
    position: absolute;
    bottom: 5px;
    right: 10px;


}
.gpt-blog-article-content p:last-child:hover{
    color: grey;
    transition: 0.3s;
}


@media screen and (max-width:1200px){
    .gpt-blog-article-content p:last-child{
        bottom: -8px;
    }

}

@media screen and (max-width:550px) {
    .gpt-blog-article-content h2{
        font-size: 18px;
        line-height: 25px;
    }
    .gpt-blog-article-content p:last-child{
        bottom: 20px;
        font-size: 10px;
        line-height: 15px;
    }
    .gpt-blog-article-content{
        height:150px;
    }
    
}