.gpt-header {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    
}
.gpt-header-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 50%;
}

.gpt-header h1 {
 font-family: var(--font-family);
 line-height: 50px;
 letter-spacing: 0.5px;
 width: 400px;
 
}
.gpt-header-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    margin: 1rem 0;
    width: 400px;

}
.gpt-header-input {
    display: flex;
    width: 100%;
    margin-top: 20px;
    box-shadow:0 0 5px;

}
.gpt-header-input input {
    flex: 1;
    width: 100%;
    padding: 5px;
    height: 50px;
    outline: none;
    border: none;
    background-color: var(--color-footer);
    color: white;

}
.gpt-header-input button {
    flex: 0.4;
    background-color: var(--color-red);
    color: white;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    outline: none;
    border: 1px solid var(--color-red);
    cursor: pointer;
    font-size: 15px;
    font-family: var(--font-family);
    
    
}
.gpt-header-input button:hover {
    color: grey;
    transition: 0.3s;
}
.gpt-header-people {
    display: flex;
    justify-content: end;
    align-items:end;
    margin-top: 40px;


}
.gpt-header-people p {
    margin: 10px 0 0 10px;
    
}

.gpt-header-image{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items:center;
    width: 50%;
    position: relative;
    bottom: 50px;
    left: 0;

}
.gpt-header-image img{
   width: 100%;
  
   
}


@media screen and (max-width: 1050px) {

  
    .gpt-header-image{
        width: 100%;
        bottom: 30px;
    }
    
    
}
@media screen and (max-width: 900px) {

    .gpt-header{
        flex-direction: column;
        margin: 0 auto;

     
    }
    .gpt-header-content {
        justify-content: center;
        align-items: center;
        width: 100%;

    }

    .gpt-header-people {
        flex-direction: column;
        margin: 10px;
       
    }
    
    .gpt-header-people img{
       margin: 20px auto;
        
    }
    .gpt-header-people p{
        text-align: center;
    }
    .gpt-header-content h1{
        line-height: 40px;
        font-size: 25px;

    }
    .gpt-header-content p {
        font-size: 12px;

    }
    .gpt-header-input{
        width: 90%;
       
    }
    
    .gpt-header-content button{
        font-size: 12px;
    }
    .gpt-header-image{
        margin: 20px auto;
       
    }
    
}
@media screen and (max-width:768px){

    .gpt-header-input {
        width: 80% ;
    }
}
@media screen and (max-width:490px) {

    .gpt-header-content h1{
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        
    }
    .gpt-header-content p{
        font-size: 10px;
    
        width: 100%;
    }
    

    
}
