.gpt-footer {
    background: var(--color-footer);
    color: var(--color-white);
}
.gpt-footer-heading {
    text-align: center;
    margin-bottom: 2rem;
}
.gpt-footer-heading h1{
    font-size: 62px;
    font-family: var(--font-family);
    font-weight: 800;
    letter-spacing: -4%;
    line-height: 75px;
  
}
.gpt-footer-btn{
    border: solid white 1px;
    margin: 70px auto ;
    padding: 20px;
    width: fit-content;

}
.gpt-footer-btn button {
    border: none;
    outline: none;
    color: var(--color-white);
    background: var(--color-footer);
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 400;
    line-height: 100%;
}
.gpt-footer-btn button:hover,
.gpt-footer-links a:hover{
    color: grey;
    transition: 0.5s;
    cursor: pointer;
}
.gpt-footer-links{
    padding-top: 35px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 25px;
    color: var(--color-white);
    width: 100%;
    text-align: left;
}
.gpt-footer-links-logo{
    margin-bottom: 5rem;
}

.gpt-footer-links-logo img{
    width: 118px;
    height: 30px;
    margin-bottom:1rem;
}
.gpt-footer-links-logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 20px;
    font-weight: 400;
    padding: 20px 0;
    max-width: 200px;
}
.gpt-footer-links h4{
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 800;
    line-height: 100%;
    margin-bottom: 1rem;

}
.gpt-footer-links a{
    font-size: 12px;
    font-family: var(--font-family);
    font-weight: 400;
    margin: 0.5rem 0;

}
.gpt-footer-links-groupA,
.gpt-footer-links-groupB,
.gpt-footer-links-groupC{
    display: flex;
    flex-direction:column;
    gap: 20px;

}
.gpt-footer-copyrights{
    margin-top: 7rem;
    text-align: center;

}
.gpt-footer-copyrights p{
    font-size: 12px;
    line-height: 14px; 
    font-family: var(--font-family);
    font-weight: 400;
    
}




@media screen and (max-width:850px) {
   
    .gpt-footer-heading h1{
        font-size: 44px;
        line-height:52px ;
    }
    .gpt-footer-links-logo{
        display: flex;
        flex-direction: column;
    }
  
}

@media screen and (max-width:550px) {
   
    .gpt-footer-heading h1{
        font-size: 34px;
        line-height:42px ;
    }
    .gpt-footer-links{
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin-left: 20px;
    }
    .gpt-footer-btn button {
        font-size: 14px;
    }

}

@media screen and (max-width:450px) {
   
    .gpt-footer-heading h1{
        font-size: 27px;
        line-height:35px ;
    }
}