.gpt-features-container {
    padding: 30px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    width: 100%;
    margin: 1rem;
  

}
.gpt-features-title {
    display: flex;
    flex-direction: column;
    gap: 10px;
    flex: 1;
    max-width: 190px;
    margin-right: 2rem;
    
}
.gpt-features-title div{
   width: 38px;
   height: 3px;
   background: var(--gradiant-text);
   box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
   margin-bottom: 0.25rem;

}
.gpt-features-title h1{
    color: white;
    font-size: 24px;
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt-features-text {
    display: flex;
    color: var(--color-text);
    flex: 2;
    max-width: 690px;
    
}
.gpt-features-text p {
    line-height: 25px;
    font-size: 14px;
    font-family: var(--font-family);
    font-weight: 500;
}

@media screen and (max-width:1050px) {

    .gpt-features-title h1{
        font-size: 20px;
    }
    .gpt-features-title div{
        left: 0;
    }
    .gpt-whatgpt-container .gpt-features-text p{
        
        height: 130px;
        text-align: start;

    }
   
}



@media screen and (max-width:768px) {
    .gpt-features-title h1{
        font-size: 19px;
    }
    .gpt-whatgpt-container .gpt-features-text p{
        
        height: 60px;
    }
    .gpt-whatgpt-container .gpt-features-container{
margin: 0 auto;
    }

    
}


@media screen and (max-width:576px) {
    .gpt-features-container {
        display: flex;
        flex-direction: column;
        position: relative;

    

    }
    
    .gpt-features-title {
      
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top:0;
        left: 0;
         transform: translateX(15%);
    
    
    }
       
    .gpt-features-title h1{
        font-size: 17px;
        
      
    }
  
 .gpt-features-text p{
        font-size: 12px;
        line-height: 25px;
        
        
    }
    .gpt-features-title div{
        display: none;
        

    }

    
}
