.gpt-blog{
    display: flex;
    flex-direction: column;
}

.gpt-blog-heading{
    width: 100%;
    margin-bottom:  3rem;
    text-align: left;

}
.gpt-blog-heading h1{
    font-size: 57px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}
.gpt-blog-container{
    display: flex;
    height: 700px;
    flex-direction: row;
    
    
}
.gpt-blog-groupA{
    flex: 0.75;
    margin-right: 2rem;
  

}
.gpt-blog-groupB {
    flex: 1;
    width:100%;
display: grid;
grid-template-columns: repeat(2,1fr);
column-gap: 2rem;
row-gap: 1rem;



}
.gpt-blog-groupB .gpt-blog-article,
.gpt-blog-groupA .gpt-blog-article-content{
    height:350px;

}
.gpt-blog-groupA img{
    height: 360px;
}
 .gpt-blog-groupB .gpt-blog-article-content{
    height: 225px;
}



@media screen and (max-width:990px){

   
    .gpt-blog-container{
       
        flex-direction: column-reverse;
        height: 100%;
       
    }
    
   
    .gpt-blog-groupA .gpt-blog-article{
        margin: 20px 0;
        width: 50%;

    }
    .gpt-blog-groupA .gpt-blog-article-content{
        height: 225px;
    }
    .gpt-blog-groupA img{
        height: 250px;
    }
    .gpt-blog-heading h1{
        font-size: 50px;
        line-height: 65px;
        
    }
    .gpt-blog-container h2{
        font-size: 17px;
        line-height: 20px;
    }


    
}
@media screen and (max-width:700px){
    .gpt-blog-groupA {
        margin: 10px auto;
    }
    .gpt-blog-groupA .gpt-blog-article,
    .gpt-blog-groupB .gpt-blog-article{

        width: 100%;
       

    }
    .gpt-blog-heading h1{
        font-size: 40px;
        line-height: 60px;
        
    }
    

}
@media screen and (max-width:550px){
    .gpt-blog-heading h1{
        font-size: 30px;
        line-height: 50px;
        
    }
    .gpt-blog-container h2{
        font-size: 15px;
        line-height: 20px;
    }
    .gpt-blog-groupB{
       display: flex;
       flex-direction: column;
       bottom: -5px;
    }

}
